.diagram-value {
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #cacace;
    font-size: 17px;
    border: 2px solid transparent;
    transition: background-color 0.3s, border-color 0.3s
}

.diagram-value {
    border-color: #222831;
    background-color: #fff
}

.diagram-values-item-horizontal {
    display: flex;
    flex-direction: column;
    justify-content: space-between
}

.diagram-values-item-horizontal__title {
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 0;
}

.round-values-list {
    justify-content: center;
    display: flex;
    align-items: center
}

.round-values-list__item {
    margin: 2px
}

.diagram-values-list {
    max-width: 470px
}

.diagram-values-list__item:not(:last-child) {
    margin-bottom: 5px
}


.diagram-values-item {
    list-style: none;
    max-width: 220px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: auto;
    margin: 20px;
    padding: 5px;
    border: 2px solid #9486b3;
    border-radius: 10px;
}

.diagram-values-item__title {
    margin-bottom: 5px
}

.diagram-values-item__description {
    font-size: 16px;
    margin-bottom: 5px;
    line-height: 1.375
}

.small-diagram-values {
    position: relative;
    display: flex;
    align-items: flex-end;
    height: 100%
}


.small-diagram-values__titles-block {
    padding-right: 25px;
    min-width: 70px
}

@media only screen and (min-width: 768px) {
    .small-diagram-values__titles-block {
        min-width: auto
    }
}

.small-diagram-values__title {
    margin-bottom: 20px
}

.small-diagram-values__title:last-child {
    margin-bottom: 0
}

.small-diagram-values__values-block {
    position: relative;
    width: 130px;
    height: 80px
}

.small-diagram-values__path-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 38px
}

.small-diagram-values__value {
    position: absolute
}

.small-diagram-values__value.-top-left {
    top: 0;
    left: 0
}

.small-diagram-values__value.-bottom-left {
    bottom: 0;
    left: 0
}

.small-diagram-values__value.-center {
    top: 50%;
    right: 0;
    transform: translate(0, -50%)
}

